import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { useHistory } from "react-router"
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import Popper from "@material-ui/core/Popper";
import PopupState, { bindToggle, bindPopper } from "material-ui-popup-state";
import Fade from "@material-ui/core/Fade";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";

const useStyles = makeStyles((theme) => ({
  typography: {
    paddingTop: theme.spacing(2.5),
    paddingLeft: theme.spacing(2.5),
    paddingRight: theme.spacing(2.5),
  },
  grid1: {
    paddingTop: theme.spacing(2.5),
    paddingBottom: theme.spacing(2.5),
  },
  button: {
    padding: theme.spacing(0.5),
    borderColor: theme.palette.base1.main,
    color: theme.palette.base1.main,
    "&:hover": {
      borderColor: theme.palette.base1.dark,
      color: theme.palette.base1.dark,
    },
  },
  button2: {
    width: '50%',
    borderRadius: 50,
    fontSize: 11,
    textTransform: "none",
    borderColor: theme.palette.base1.main,
    color: theme.palette.base1.main,
    "&:hover": {
      borderColor: theme.palette.info.dark,
      color: theme.palette.info.dark,
    },
  },
  button3: {
    width: '50%',
    borderRadius: 50,
    fontSize: 11,
    textTransform: "none",
    backgroundColor: theme.palette.info.main,
    borderColor: theme.palette.info.main,
    color: theme.palette.paper,
    "&:hover": {
      borderColor: theme.palette.base1.dark,
      color: theme.palette.paper,
    },
  },
  popper: {
    width: "15%",
    boxShadow: "0 3px 10px rgb(0 0 0 / 0.2)",
  },
}));

export default function PopperPopupState() {
  const classes = useStyles();
  const history = useHistory();

  const redirectCheckout = () => {
    history.push('/checkout')
  }

  return (
    <PopupState variant="popper" popupId="demo-popup-popper">
      {(popupState) => (
        <div>
          <Button
            fullWidth
            className={classes.button}
            variant="outlined"
            color="primary"
            {...bindToggle(popupState)}
          >
            Book Turf 2
          </Button>
          <Popper
            {...bindPopper(popupState)}
            transition
            className={classes.popper}
            modifiers={{
              arrow: {
                enabled: true,
              },
            }}
          >
            {({ TransitionProps }) => (
              <Fade {...TransitionProps} timeout={350}>
                <Paper>
                  <Typography className={classes.typography}>Tue, 20-Oct</Typography>
                  <Typography variant="caption" className={classes.typography}>
                    6:30 AM - 7:00 AMs
                  </Typography>
                  <Grid container justify="space-between">
                    <Typography inline variant="body1" align="left" className={classes.typography}>
                      Turf 2
                    </Typography>
                    <Typography inline variant="body1" align="right" className={classes.typography}>
                      650
                    </Typography>
                  </Grid>
                  <Grid className={classes.grid1} container justify="space-between">
                    <Button className={classes.button2} variant="outlined" align="left" color="primary">
                      Add to cart
                    </Button>
                    <Button onClick={redirectCheckout} className={classes.button3} variant="contained" align="right" color="primary">
                      Book Now
                    </Button>
                  </Grid>
                </Paper>
              </Fade>
            )}
          </Popper>
        </div>
      )}
    </PopupState>
  );
}
