import React from "react";
import Header from "./Header";
import Default from "./Default";
import List from "./List";

export default function Slots() {
  const [stage, setStage] = React.useState(0);
  const [userInput, setUserInput] = React.useState({});

  const changeState = (data) => {
    if (data.sports && data.date && data.confirm) {
      setUserInput(data);
      setStage(2);
    } else if (data.sports && data.date) {
      setUserInput(data);
      setStage(1);
    } else {
      setUserInput({});
      setStage(0);
    }
  };

  const SwitchComponent = () => {
    switch (stage) {
      case 0:
        return <Default />;
      case 1:
        return <List userInput={userInput} />;
      default:
        return <Default />;
    }
  };

  return (
    <>
      <Header changeState={(data)=>changeState(data)} />
      <SwitchComponent />
    </>
  );
}
