import React from "react";
import { useHistory } from "react-router";
import { makeStyles } from "@material-ui/core/styles";
import Header from "./Header";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import { Paper } from "@material-ui/core";
import ControlPointIcon from "@material-ui/icons/ControlPoint";
import IconButton from "@material-ui/core/IconButton";
import DeleteOutlineIcon from "@material-ui/icons/DeleteOutline";
import EditOutlinedIcon from "@material-ui/icons/EditOutlined";
import CardContent from "@material-ui/core/CardContent";
import Card from "@material-ui/core/Card";

import Modal from "../common/Modal";

const useStyles = makeStyles((theme) => ({
  right: {
    marginLeft: "auto",
  },
  grid: {
    padding: theme.spacing(3),
    paddingBottom: "5px !important",
    width: "60%",
    [theme.breakpoints.down("md")]: {
      width: "100%",
    },
  },
  card: {
    minWidth: "100%",
  },
  cardContent: {
    display: "flex",
  },
  typo1: {
    fontSize: "15px",
    color: "#272727",
    fontWeight: "600",
    display: "inline",
  },
  typo2: {
    color: "#808080",
  },
  icon: {
    color: "#272727",
  },
}));

const PricingList = () => {
  const classes = useStyles();
  const history = useHistory();
  const [open, setOpen] = React.useState(false);

  const AddPricing = () => {
    history.push("/pricing/add");
  };

  const DeletePricing = () => {
    console.log('true');
    setOpen(true);
  };

  const setFunctionOpen = (state) => {
    setOpen(state);
  };

  const PriceType = () => {
    return (
      <Card className={classes.card} variant="outlined">
        <CardContent className={classes.cardContent}>
          <Grid>
            <Typography variant="h6" component="h6">
              Monday - Friday
            </Typography>
          </Grid>
          <Grid className={classes.right}>
            <IconButton
              onClick={DeletePricing}
              className={classes.icon}
              aria-label="Delete Pricing"
              component="span"
            >
              <DeleteOutlineIcon />
            </IconButton>
            <IconButton className={classes.icon} aria-label="Delete Pricing" component="span">
              <EditOutlinedIcon />
            </IconButton>
          </Grid>
        </CardContent>
        <CardContent className={classes.cardContent}>
          <Grid>
            <Typography className={classes.typo2} variant="h6" component="h6">
              8:30 pm - 9:30 pm
            </Typography>
          </Grid>
          <Grid className={classes.right}>
            <Typography className={classes.typo2} variant="h6" component="h6">
              Rs 450
            </Typography>
          </Grid>
        </CardContent>
      </Card>
    );
  };

  return (
    <div>
      <Modal open={open} setFunctionOpen={(state) => setFunctionOpen(state)} title="Delete Pricing" description="You want to delete Regular Pricing?"/>
      <Header />
      <Paper>
        <Grid className={classes.grid} container alignItems="center">
          <Grid>
            <Typography variant="h6" component="h6">
              REGULAR
            </Typography>
          </Grid>
          <Grid className={classes.right}>
            <IconButton
              onClick={AddPricing}
              className={classes.icon}
              aria-label="create pricing"
              component="span"
            >
              <ControlPointIcon />
            </IconButton>
            <Typography className={classes.typo1}>ADD PRICING</Typography>
          </Grid>
        </Grid>
        <Grid className={classes.grid} container alignItems="center">
          <PriceType />
        </Grid>
      </Paper>
    </div>
  );
};

export default PricingList;
