import React from "react";
import Default from "./Default";

export default function Slots() {
  const [stage, setStage] = React.useState(0);
  const [userInput, setUserInput] = React.useState({});

  const changeState = (data) => {
    if (data.sports && data.date && data.confirm) {
      setUserInput(data);
      setStage(2);
    } else if (data.sports && data.date) {
      setUserInput(data);
      setStage(1);
    } else {
      setUserInput({});
      setStage(0);
    }
  };

  return (
    <>
       <Default changeState={(data)=>changeState(data)} />
    </>
  );
}
