import React from "react";
import { BrowserRouter, Route, Redirect, Switch } from "react-router-dom";
import "./assets/css/Calendar.css";
import { ThemeProvider } from "@material-ui/core/styles";
import Theme from "./assets/theme/Theme";
import { makeStyles } from "@material-ui/core/styles";
// Views
import Drawer from "./layouts/Drawer";
import NotFound from "./layouts/NotFound";
// Schedule

import Slots from "./components/schedule/slots/Index";
import Bulk from "./components/schedule/bulkBooking/Index";
import Checkout from "./components/common/Checkout";
import Pricing from "./components/pricing/Index";
import PricingList from "./components/pricing/List";
import PricingAdd from "./components/pricing/Add";

import MembersList from "./components/members/members/list/Index";
import MembersAdd from "./components/members/members/add/Index";
import MembersPackages from "./components/members/membershipPackages/list/Index";
import MembersPackagesAdd from "./components/members/membershipPackages/add/Index";
import Report from "./components/report/Index";
import ReportList from "./components/report/List";
import OfferList from "./components/offer/list/Index";
import OfferAdd from "./components/offer/add/Index";

const useStyles = makeStyles((theme) => ({
  toolbar: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    [theme.breakpoints.up("sm")]: {
      marginLeft: "280px",
      width: "calc(100% - 280px)",
    },
    padding: theme.spacing(0),
  },
}));

const App = () => {
  const classes = useStyles();
  return (
    <ThemeProvider theme={Theme()}>
      <BrowserRouter>
        <Drawer />
        <main className={classes.content}>
          <div className={classes.toolbar} />
          <Switch>
            <Route path="/" exact render={(props) => <Slots {...props} />} />
            <Route path="/slots" exact render={(props) => <Slots {...props} />} />
            <Route path="/bulk" exact render={(props) => <Bulk {...props} />} />
            <Route path="/checkout" exact render={(props) => <Checkout {...props} />} />
            <Route path="/pricing" exact render={(props) => <Pricing {...props} />} />
            <Route path="/pricing/list" exact render={(props) => <PricingList {...props} />} />
            <Route path="/pricing/add" exact render={(props) => <PricingAdd {...props} />} />
            <Route path="/members" exact render={(props) => <MembersList {...props} />} />
            <Route path="/members/add" exact render={(props) => <MembersAdd {...props} />} />
            <Route
              path="/members/packages"
              exact
              render={(props) => <MembersPackages {...props} />}
            />
            <Route
              path="/members/packages/add"
              exact
              render={(props) => <MembersPackagesAdd {...props} />}
            />
            <Route path="/report" exact render={(props) => <Report {...props} />} />
            <Route path="/report/list" exact render={(props) => <ReportList {...props} />} />
            <Route path="/offers" exact render={(props) => <OfferList {...props} />} />
            <Route path="/offers/add" exact render={(props) => <OfferAdd {...props} />} />
            <Route path="/pagenotfound" exact render={(props) => <NotFound {...props} />} />
            <Redirect to="/slots" />
          </Switch>
        </main>
      </BrowserRouter>
    </ThemeProvider>
  );
};

export default App;
