import React from "react";
import { Divider, Paper } from "@material-ui/core";
import { Grid } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import FormControl from "@material-ui/core/FormControl";
import FormGroup from "@material-ui/core/FormGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import Button from "@material-ui/core/Button";

const useStyles = makeStyles((theme) => ({
  paper: {
    padding: theme.spacing(3),
  },
  typo1: {
    color: "#484848",
    fontSize: "16px",
    fontWeight: "600",
  },
  typo2: {
    color: "#464646",
    fontSize: "20px",
  },
  textField: {
    width: "95%",
    marginTop: theme.spacing(1),
  },
  grid: {
    marginTop: theme.spacing(2),
  },
  grid2: {
    marginTop: theme.spacing(0.5),
    display: "flex",
    marginRight: theme.spacing(3),
  },
  grid3: {
    marginLeft: "auto",
  },
  divider: {
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(2),
  },
  button: {
    borderRadius: 50,
    marginTop: theme.spacing(2),
    backgroundColor: theme.palette.base1.main,
    color: "#fff",
    "&:hover": {
      backgroundColor: theme.palette.base1.dark,
    }
  }
}));

const Form = () => {
  const [mem, setMem] = React.useState("");

  const handleChange = (event) => {
    setMem(event.target.value);
  };
  const classes = useStyles();
  const [state, setState] = React.useState({
    gilad: true,
    jason: false,
    antoine: false,
  });

  const handleChangeChecked = (event) => {
    setState({ ...state, [event.target.name]: event.target.checked });
  };

  const { gilad, jason, antoine } = state;
  return (
    <Paper className={classes.paper}>
      <form>
        <Grid className={classes.grid} container lg={8}>
          <Grid lg={6}>
            <Typography className={classes.typo1}>MEMBER NAME</Typography>
            <TextField
              className={classes.textField}
              id="name"
              label="Enter your full name"
              defaultValue=""
              variant="outlined"
              size="small"
              type="input"
            />
          </Grid>
          <Grid lg={6}>
            <Typography className={classes.typo1}>PHONE NUMBER</Typography>
            <TextField
              className={classes.textField}
              id="number"
              label="+91"
              defaultValue=""
              variant="outlined"
              size="small"
              type="input"
            />
          </Grid>
        </Grid>
        <Grid className={classes.grid} container lg={8}>
          <Grid lg={6}>
            <Typography className={classes.typo1}>EMAIL ID</Typography>
            <TextField
              className={classes.textField}
              id="email"
              label="example@gmail.com"
              defaultValue=""
              variant="outlined"
              size="small"
              type="input"
            />
          </Grid>
          <Grid lg={6}>
            <Typography className={classes.typo1}>MEMBERSHIP TYPE</Typography>
            <FormControl className={classes.textField} variant="outlined" size="small">
              <InputLabel id="memtype">5 Year Package</InputLabel>
              <Select
                labelId="memtype"
                id="memtypedropdown"
                value={mem}
                onChange={handleChange}
                label="MembershipType"
              >
                <MenuItem value="">
                  <em>None</em>
                </MenuItem>
                <MenuItem value={1}>1 Year Package</MenuItem>
                <MenuItem value={2}>5 Year Package</MenuItem>
                <MenuItem value={3}>Life Time Package</MenuItem>
              </Select>
            </FormControl>
          </Grid>
        </Grid>
        <Grid className={classes.grid} container lg={8}>
          <Grid lg={6}>
            <Typography className={classes.typo1}>ADDRESS</Typography>
            <TextField
              className={classes.textField}
              id="address"
              label="F-402..."
              defaultValue=""
              variant="outlined"
              size="small"
              multiline
              rows={4}
              type="input"
            />
          </Grid>
          <Grid lg={6}>
            <Typography className={classes.typo1}>GENDER</Typography>
            <FormControl
              className={classes.textField}
              required
              component="fieldset"
              className={classes.formControl}
            >
              <FormGroup>
                <FormControlLabel
                  control={<Checkbox checked={gilad} onChange={handleChangeChecked} name="gilad" />}
                  label="Male"
                />
                <FormControlLabel
                  control={<Checkbox checked={jason} onChange={handleChangeChecked} name="jason" />}
                  label="Female"
                />
                <FormControlLabel
                  control={
                    <Checkbox checked={antoine} onChange={handleChangeChecked} name="antoine" />
                  }
                  label="Other"
                />
              </FormGroup>
            </FormControl>
          </Grid>
          <Grid className={classes.grid2} container lg={12}>
            <Typography variant="h6" className={classes.typo1}>
              PAYMENT DETAILS
            </Typography>
          </Grid>
          <Grid className={classes.grid2} container lg={12}>
            <Grid>
              <Typography className={classes.typo1}>5 year package</Typography>
            </Grid>
            <Grid className={classes.grid3}>
              <Typography className={classes.typo2}>5,00,000</Typography>
            </Grid>
          </Grid>
          <Grid className={classes.grid2} container lg={12}>
            <Grid>
              <Typography className={classes.typo1}>GST(18%)</Typography>
            </Grid>
            <Grid className={classes.grid3}>
              <Typography className={classes.typo2}>40,000</Typography>
            </Grid>
          </Grid>
          <Divider className={classes.divider} />
          <Grid className={classes.grid2} container lg={12}>
            <Grid>
              <Typography className={classes.typo1}>Total amount</Typography>
            </Grid>
            <Grid className={classes.grid3}>
              <Typography className={classes.typo2}>5,40,000</Typography>
            </Grid>
          </Grid>
        </Grid>
        <Grid className={classes.grid} container lg={8}>
          <Grid lg={6}>
            <Typography className={classes.typo1}>PAYMENT TYPE</Typography>
            <FormControl className={classes.textField} variant="outlined" size="small">
              <InputLabel id="memtype">Cash / Card</InputLabel>
              <Select
                labelId="memtype"
                id="memtypedropdown"
                value={mem}
                onChange={handleChange}
                label="MembershipType"
              >
                <MenuItem value="">
                  <em>None</em>
                </MenuItem>
                <MenuItem value={1}>Cash</MenuItem>
                <MenuItem value={2}>Card</MenuItem>
              </Select>
            </FormControl>
          </Grid>
          <Grid lg={6}>
            <Typography className={classes.typo1}>.</Typography>
            <TextField
              className={classes.textField}
              id="total"
              label="Total Amount"
              defaultValue=""
              variant="outlined"
              size="small"
              type="input"
            />
          </Grid>
        </Grid>
        <Grid className={classes.grid} container lg={8}>
          <Grid lg={6}>
            <Typography className={classes.typo1}>HANDLER'S NAME</Typography>
            <TextField
              className={classes.textField}
              id="hname"
              label="Karan"
              defaultValue=""
              variant="outlined"
              size="small"
              type="input"
            />
          </Grid>
        </Grid>
        <Button className={classes.button} variant="contained">
          CONFIRM MEMBERSHIP
        </Button>
      </form>
    </Paper>
  );
};

export default Form;
