import React from "react";

import FormControl from "@material-ui/core/FormControl";
import NativeSelect from "@material-ui/core/NativeSelect";
import InputBase from "@material-ui/core/InputBase";
import { makeStyles, withStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";

const useStyles = makeStyles((theme) => ({
  paper: {
    padding: theme.spacing(3),
    backgroundColor: "#F5F5F5",
    borderColor: "#fff",
    display: "flex",
  },
  typo1: {
    color: "#484848",
    verticalAlign: "middle",
    fontWeight: "bold",
  },
  typo2: {
    color: "#484848",
    verticalAlign: "middle",
    fontWeight: "bold",
  },
  divider: {
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1),
  },
}));

const BootstrapInput = withStyles((theme) => ({
  root: {
    marginTop: theme.spacing(1),
    minWidth: "200px",
  },
  input: {
    borderRadius: 1,
    position: "relative",
    backgroundColor: theme.palette.background.paper,
    border: "1.5px solid #040F33",
    fontSize: 16,
    padding: "10px 26px 10px 12px",
    transition: theme.transitions.create(["border-color", "box-shadow"]),
  },
}))(InputBase);

const SelectPricing = (props) => {
  const { data, funct, getSelectValue, title } = props;
  const [value, setValue] = React.useState(1);

  const classes = useStyles();

  const handleChange = (event) => {
    setValue(event.target.value);
    let data = {};
    data[funct] = event.target.value;
    getSelectValue(data);
  };

  return (
    <FormControl>
      <Typography className={classes.typo2}>{title}</Typography>
      <NativeSelect id="select" value={value} onChange={handleChange} input={<BootstrapInput />}>
        <option aria-label="None" value="" />
        {data.map((ele) => {
          return <option value={ele.id}>{ele.name}</option>;
        })}
      </NativeSelect>
    </FormControl>
  );
};

export default SelectPricing;
