import React from "react";
import { Grid } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import Button from "@material-ui/core/Button";

const useStyles = makeStyles((theme) => ({
  grid: {
    padding: theme.spacing(3),
  },
  typo1: {
    marginTop: theme.spacing(1.5),
    color: "#484848",
    fontSize: "16px",
    fontWeight: "600",
  },
  textField: {
    width: "95%",
    marginTop: theme.spacing(1),
  },
  button: {
    borderRadius: 50,
    marginTop: theme.spacing(2),
    backgroundColor: theme.palette.base1.main,
    color: "#fff",
    "&:hover": {
      backgroundColor: theme.palette.base1.dark,
    },
  },
}));

const Form = () => {
  const [mem, setMem] = React.useState("");
  const handleChange = (event) => {
    setMem(event.target.value);
  };
  const classes = useStyles();
  return (
    <Grid className={classes.grid}>
      <Grid lg={4}>
        <Typography className={classes.typo1}>PACKAGE NAME</Typography>
        <TextField
          className={classes.textField}
          id="name"
          label="ENTER PACKAGE NAME"
          defaultValue=""
          variant="outlined"
          size="small"
          type="input"
        />
      </Grid>
      <Grid lg={4}>
        <Typography className={classes.typo1}>PACKAGE DURATION</Typography>
        <FormControl className={classes.textField} variant="outlined" size="small">
          <InputLabel id="memtype">5 Year Package</InputLabel>
          <Select
            labelId="memtype"
            id="memtypedropdown"
            value={mem}
            onChange={handleChange}
            label="MembershipType"
          >
            <MenuItem value="">
              <em>None</em>
            </MenuItem>
            <MenuItem value={1}>1 Year Package</MenuItem>
            <MenuItem value={2}>5 Year Package</MenuItem>
          </Select>
        </FormControl>
      </Grid>
      <Grid lg={4}>
        <Typography className={classes.typo1}>PACKAGE PRICING</Typography>
        <TextField
          className={classes.textField}
          id="total"
          label="450"
          defaultValue=""
          variant="outlined"
          size="small"
          type="input"
        />
      </Grid>
      <Grid>
        <Button variant="outlined" className={classes.button}>
          CONFIRM PACKAGE
        </Button>
      </Grid>
    </Grid>
  );
};

export default Form;
